import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { ProductsService } from './common/services/products.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],

})
export class AppComponent implements OnInit {
  public title: string = 'Angular course';
  public logo: string = 'data:image/svg+xml;base64,';
  public placeholder: string = 'search term';
  public text: string; // not need but was in lesson
  public sortOrder: boolean = true;
  public width: number = 50;
  public desc: string = 'it is logo';

  public products$: Observable<Product[]>;

  public constructor( private _productsService: ProductsService) {}

  public ngOnInit(): void {
     this.products$ = this._productsService.getProducts();
  }

  public clickOnImage(): void {
    console.log('click');
  }

  public changeSortOrder(): void {
    this.sortOrder = !this.sortOrder;
  }
}




